import Knex from "knex";
import { useState, useRef } from "react";
import SQLJSClient from "../utils/SQLJSClient";
import * as greldal from "../../../lib";
import * as graphql from "graphql";
import Loadable from "react-loadable";
import SplitPane from "react-split-pane";
import styled from "styled-components";
import logo from "../assets/logo.png";

import "../styles/split-pane.css";

const Loading = () => <div>Loading...</div>;

const CodeMirror = Loadable.Map({
    loader: {
        ReactCodeMirror: () => import("react-codemirror"),
        resources: () =>
            Promise.all([
                import("codemirror/lib/codemirror.css"),
                import("codemirror/mode/javascript/javascript"),
                import("codemirror/theme/monokai.css"),
                import("codemirror/addon/hint/show-hint"),
                import("codemirror/addon/lint/lint"),
                import("codemirror-graphql/hint"),
                import("codemirror-graphql/lint"),
                import("codemirror-graphql/mode"),
            ]),
    },
    render(loaded, { innerRef, ...props }) {
        let ReactCodeMirror = loaded.ReactCodeMirror.default;
        return <ReactCodeMirror {...props} ref={innerRef} />;
    },
    loading: Loading,
});

const AsyncFunction = Object.getPrototypeOf(eval(`(async function __test() {})`)).constructor;

if (typeof window !== "undefined") {
    window.Knex = Knex;
    window.greldal = greldal;
}

const defaultCode = `
greldal.useDatabaseConnector(knex);

await knex.schema.createTable("customers", t => {
    t.increments("pk");
    t.string("first_name");
    t.string("last_name");
});

await knex("customers").insert([
    {first_name: "Harry", last_name: "Granger"},
    {first_name: "Ron", last_name: "Potter"}
]);

const fields = greldal.mapFields({
    id: {
        sourceColumn: "pk",
        type: greldal.types.number,
        to: {
            input: graphql.GraphQLID,
            output: graphql.GraphQLID,
        },
    },
    firstName: {
        sourceColumn: "first_name",
        type: greldal.types.string,
    },
    lastName: {
        sourceColumn: "last_name",
        type: greldal.types.string,
    },
});

const users = greldal.mapDataSource({
    name: {
        mapped: "User",
        stored: "customers",
    },
    fields,
});

const schema = greldal.mapSchema(greldal.operationPresets.defaults(users));

return schema;
`;

const defaultQuery = `
query { 
    findManyUsers(where: {}) { 
        id, 
        firstName, 
        lastName 
    }
}
`;

export default function() {
    const [code, setCode] = useState(defaultCode);
    const [schema, setSchema] = useState(null);
    const [query, setQuery] = useState(defaultQuery);
    const [result, setResult] = useState("");
    const codeEditorRef = useRef(null);
    const gqlEditorRef = useRef(null);
    const resultRef = useRef(null);

    const runCode = async () => {
        const knex = Knex({
            client: SQLJSClient,
            debug: true,
            pool: { min: 1, max: 1 },
            acquireConnectionTimeout: 500,
        });
        knex.initialize();
        const run = new AsyncFunction("Knex", "knex", "greldal", "graphql", code);
        try {
            const schema = await run(Knex, knex, greldal, graphql);
            setSchema(schema);
        } catch (e) {
            console.error(e);
        }
    };

    const queryAPI = () => {
        if (!schema || !query) return;
        graphql
            .graphql(schema, query)
            .then(res => setResult(res))
            .catch(e => console.error(e));
    };

    const refreshEditors = () => {
        for (const ref of [codeEditorRef, gqlEditorRef]) {
            if (!ref.current) continue;
            const editor = ref.current.getCodeMirror();
            if (!editor) continue;
            editor.refresh();
        }
    };

    return (
        <PageContainer>
            <SplitPane split="vertical" minSize={50} defaultSize="50%" onChange={refreshEditors}>
                <PaneBody>
                    <HeaderInfo>
                        <a href={ROOT_PATH} style={{ display: "block", marginRight: "10px" }}>
                            <img src={logo} style={{ height: "50px", width: "50px" }} />
                        </a>
                        <div>
                            GRelDAL Playground is an <strong>experimental</strong> sandboxed environment where you can
                            play around with a GraphQL API powered by GRelDAL and sql.js within your browser without
                            having to install anything.
                        </div>
                        <div
                            style={{
                                flexBasis: "170px",
                                flexGrow: "0",
                                flexShrink: "0",
                            }}
                        >
                            <PrimaryBtn style={{ marginLeft: "10px" }} onClick={runCode}>
                                Generate Schema ⇨
                            </PrimaryBtn>
                        </div>
                    </HeaderInfo>
                    <div style={{ position: "relative", flexGrow: 2, flexShrink: 1, overflow: "hidden" }}>
                        <CodeMirror
                            options={{
                                scrollbarStyle: "native",
                                theme: "monokai",
                                mode: "javascript",
                                lineNumbers: true,
                            }}
                            value={code}
                            onChange={setCode}
                            innerRef={codeEditorRef}
                        />
                    </div>
                </PaneBody>
                <SplitPane split="horizontal" defaultSize="50%" onChange={refreshEditors}>
                    <PaneBody>
                        {schema ? (
                            <>
                                <HeaderInfo>
                                    <div>Query your API using GraphQL</div>
                                    <PrimaryBtn style={{ float: "right", marginLeft: "10px" }} onClick={queryAPI}>
                                        Run Query ⇩
                                    </PrimaryBtn>
                                </HeaderInfo>
                                <EditorContainer>
                                    <CodeMirror
                                        options={{
                                            theme: "monokai",
                                            mode: "graphql",
                                            lint: {
                                                schema,
                                            },
                                            hintOptions: {
                                                schema,
                                            },
                                            lineNumbers: true,
                                        }}
                                        value={query}
                                        onChange={setQuery}
                                        innerRef={gqlEditorRef}
                                    />
                                </EditorContainer>
                            </>
                        ) : (
                            <BlankPanel>
                                Once your Schema has been prepared, you will be able to query it from this panel.
                            </BlankPanel>
                        )}
                    </PaneBody>
                    <div>
                        {result ? (
                            <CodeMirror
                                options={{
                                    scrollbarStyle: "native",
                                    theme: "monokai",
                                    mode: "javascript",
                                    readonly: true,
                                    lineNumbers: true,
                                }}
                                value={JSON.stringify(result, null, 2)}
                                innerRef={resultRef}
                            />
                        ) : (
                            <BlankPanel>Once you perform a query, you will be able to see the results here</BlankPanel>
                        )}
                    </div>
                </SplitPane>
            </SplitPane>
        </PageContainer>
    );
}

const PageContainer = styled.div`
    position: absolute;
    top: 4px;
    left: 0;
    right: 0;
    bottom: 0;

    .ReactCodeMirror {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    .CodeMirror {
        height: 100%;
        width: 100%;
    }
`;

const HeaderInfo = styled.div`
    padding: 10px;
    background: lemonchiffon;
    color: #404040;
    border-bottom: 2px solid #8dd35f;
    display: flex;
    flex-direction: row;
    flex-grow: 0;
    flex-shrink: 0;
`;

const EditorContainer = styled.div`
    position: relative;
    flex-grow: 2;
    flex-shrink: 1;
    overflow: hidden;
`;

const PrimaryBtn = styled.button`
    background: #5aac31;
    border: 1px solid #abe081;
    border-radius: 4px;
    padding: 10px;
    color: white;
    cursor: pointer;
`;

const BlankPanel = styled.div`
    color: #a7a6a6;
    font-size: 1.5rem;
    padding: 50px;
    line-height: 2rem;
    background: #ddd;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
`;

const PaneBody = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
`;
